<script setup>
	import {
		ref,
		getCurrentInstance,
		nextTick,
        computed,
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
    import {
        useStore
    } from 'vuex';
    const store = useStore()
    const user = computed(()=>store.getters['user/session'])
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const router = useRouter()
	const route = useRoute()
	//
	const tableName = 'yinpinxinxi'
	const formName = ''
	//
	const breadList = ref([{
		name: formName
	}])
	const list = ref([])
	const listQuery = ref({
		page: 1,
		limit: Number(12)
	})
	const total = ref(0)
	const listLoading = ref(false)
	//
	const btnAuth = (e,a)=>{
		if(centerType.value){
			return context?.$toolUtil.isBackAuth(e,a)
		}else{
			return context?.$toolUtil.isAuth(e,a)
		}
	}
	const addClick = () => {
		router.push('/index/yinpinxinxiAdd')
	}
	//
	const centerType = ref(false)
	//
	const backClick = () => {
		router.push(`/index/${context?.$toolUtil.storageGet('frontSessionTable')}Center`)
	}
	//
	const searchQuery = ref({})
	//
	const yinpinfenleiLists = ref([])
	const getyinpinfenleiLists = () => {
		context?.$http({
			url: 'option/yinpinfenlei/yinpinfenlei',
			method:'get'
		}).then(res=>{
			yinpinfenleiLists.value = res.data.data
		})
	}
	getyinpinfenleiLists()
	const guigeLists = ref([])
	const getguigeLists = () => {
		guigeLists.value = ',,'.split(',')
	}
	getguigeLists()
	const lengreyinLists = ref([])
	const getlengreyinLists = () => {
		lengreyinLists.value = ',,,,'.split(',')
	}
	getlengreyinLists()
	const tianduLists = ref([])
	const gettianduLists = () => {
		tianduLists.value = '(),,'.split(',')
	}
	gettianduLists()
	const searchClick = () => {
		listQuery.value.page = 1
		getList()
	}
	//
	const layouts = ref(["total","prev","pager","next","sizes","jumper"])
	const sizeChange = (size) => {
		listQuery.value.limit = size
		getList()
	}
	const currentChange = (page) => {
		listQuery.value.page = page
		getList()
	}
	//
    const sortType = ref('')
    const sortOrder = ref('')
    const sortClick = (type)=>{
        if(sortType.value==type && sortOrder.value=='asc'){
            sortType.value = ''
            sortOrder.value = ''
        }else if(sortType.value==type && sortOrder.value=='desc'){
            sortOrder.value = 'asc'
        }else{
            sortType.value = type
            sortOrder.value = 'desc'
        }
        getList()
    }
	//
	const getList = () => {
		listLoading.value = true
		let params = JSON.parse(JSON.stringify(listQuery.value))
		if (categoryIndex.value != -1) {
			params.dianpufenlei = categoryList.value[categoryIndex.value]
		}
		if(searchQuery.value.yinpinfenlei&&searchQuery.value.yinpinfenlei!=''){
			params.yinpinfenlei = searchQuery.value.yinpinfenlei
		}
		if(searchQuery.value.guige&&searchQuery.value.guige!=''){
			params.guige = searchQuery.value.guige
		}
		if(searchQuery.value.lengreyin&&searchQuery.value.lengreyin!=''){
			params.lengreyin = searchQuery.value.lengreyin
		}
		if(searchQuery.value.tiandu&&searchQuery.value.tiandu!=''){
			params.tiandu = searchQuery.value.tiandu
		}
        if(sortType.value){
            params['sort'] = sortType.value
            params['order'] = sortOrder.value
        }
        if(context.$toolUtil.storageGet('frontToken')) {
            if(user.value.dianpumingcheng){
                params['dianpumingcheng'] = user.value.dianpumingcheng
            }
        }
		context?.$http({
			url: `${tableName}/${centerType.value?'page':'list'}`,
			method: 'get',
			params: params
		}).then(res => {
			listLoading.value = false
			list.value = res.data.data.list
			total.value = Number(res.data.data.total)
		})
	}
	//
	const categoryList = ref([])
	const categoryIndex = ref(-1)
	const getCategoryList = () => {
		context?.$http({
			url: 'option/dianpufenlei/dianpufenlei',
			method: 'get'
		}).then(res => {
			categoryList.value = res.data.data
		})
	}
	const categoryClick = (index) => {
		listQuery.value.page = 1
		categoryIndex.value = index
		getList()
	}
	const detailClick = (id) => {
		router.push(`${tableName}Detail?id=` + id + (centerType.value?'&&centerType=1':''))
	}
	//
	const download = (file) =>{
		if(!file){
			context?.$toolUtil.message('','error')
		}
		const a = document.createElement('a');
		a.style.display = 'none';
		a.setAttribute('target', '_blank');
		file && a.setAttribute('download', file);
		a.href = context?.$config.url + file;
		document.body.appendChild(a);
		a.click();
		document.body.removeChild(a);
	}
	// 
	const preViewUrl = ref('')
	const preViewVisible = ref(false)
	const preViewClick = (url) =>{
		preViewUrl.value = url
		preViewVisible.value = true
	}
	const init = async() => {
		if(route.query.centerType){
			centerType.value = true
		}
		guigeLists.value = ',,'.split(',')
		lengreyinLists.value = ',,,,'.split(',')
		tianduLists.value = '(),,'.split(',')
		getCategoryList()
		getList()
	}
	init()
</script>
